* {

  margin-top: 5px;
  box-sizing: border-box;
}

.dashboard {
  display: flex;
  justify-content: space-around;
  text-decoration: none;
  background-color: aqua;
  color: white;
  align-items: stretch;
  gap: 3rem;
  padding: 0 25rem;
  padding-bottom: 5px;

}

.App {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

table {
  border: 2px solid forestgreen;
  width: 800px;
  height: 200px;
}

th {
  border-bottom: 1px solid black;
}

td {
  text-align: center;
}